import React, { Component } from 'react';
import UpdateSignal from '../Signal-list/index';
import MultiTabs from '../../../Molecules/multi-tabs';

class Criteria extends Component {
  state = {
    instanceData: JSON.parse(JSON.stringify(this.props.instanceData)),
  };

  updateState = instanceData => this.setState({ instanceData });

  tabList = (instanceData = []) => {
    return [
      {
        label: 'Entry',
        childtab: ['FLIGHT', 'PAX', 'CREW', 'BAG', 'ENGG', 'GROUND'],
        active: 'FLIGHT',
        Component: props => (
          <UpdateSignal
            instanceData={instanceData}
            updateState={this.updateState}
            {...props}
            {...this.props}
          />
        ),
      },
      {
        label: 'Exit',
        childtab: ['FLIGHT', 'PAX', 'CREW', 'BAG', 'ENGG', 'GROUND'],
        active: 'FLIGHT',
        Component: props => (
          <UpdateSignal
            instanceData={instanceData}
            updateState={this.updateState}
            {...props}
            {...this.props}
          />
        ),
      },
      {
        label: 'Reinstate',
        childtab: ['FLIGHT', 'PAX', 'CREW', 'BAG', 'ENGG', 'GROUND'],
        active: 'FLIGHT',
        Component: props => (
          <UpdateSignal
            instanceData={instanceData}
            updateState={this.updateState}
            {...props}
            {...this.props}
          />
        ),
      },
      {
        label: 'Schedule',
        childtab: ['FLIGHT', 'PAX', 'CREW', 'BAG', 'ENGG', 'GROUND'],
        active: 'FLIGHT',
        Component: props => {
          console.log(props);
          return (
            <UpdateSignal
              instanceData={instanceData}
              updateState={this.updateState}
              {...props}
              {...this.props}
            />
          );
        },
      },
      {
        label: 'Subscribe',
        childtab: [],
        active: 'FLIGHT',
        Component: props => (
          <UpdateSignal
            instanceData={JSON.parse(JSON.stringify(instanceData))}
            updateState={this.updateState}
            {...props}
            {...this.props}
          />
        ),
      },
    ];
  };

  saveCriteria = () => {};

  render() {
    if (!this.state.instanceData['schedulingCondition']) {
      const instanceData = this.state.instanceData;
      instanceData['schedulingCondition'] = {
        scheduleKey: '',
        snoozesTimeValueMins: 0,
        snoozesTimeIncrementValueMins: 0,
        operator: 'ADD',
      };
      this.setState({ instanceData });
    }
    // if (this.state.isEnterprisePage) {
    //   return (
    //     <div className="enterprise-page-in-event-config">
    //       <div className="container">sdfg</div>
    //     </div>
    //   );
    // }

    return (
      <MultiTabs
        className="event-edit"
        tabData={this.tabList(this.state.instanceData, this.props)}
        isEditPage={this.props.page === 'EDIT' ? true : false}
      />
    );
  }
}

export default Criteria;
