import React from 'react';
import './Toggler.scss';

export default class Toggler extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      value: this.props.value,
    };
  }

  togglerchange = value => {
    this.setState(
      prevState => {
        return { value };
      },
      () => {
        if (
          this.props.onoffstatus &&
          typeof this.props.onoffstatus == 'function'
        ) {
          this.props.onoffstatus(this.state.value);
        }
      }
    );
  };

  render() {
    return (
      <div className={'Toggle ' + this.props.DataToggle}>
        <div
          className={
            this.props.value === this.props.content[0]
              ? 'Toggler togglerchange'
              : 'Toggler'
          }
          onClick={() => this.togglerchange(this.props.content[0])}
        >
          {this.props.content[0]}
        </div>
        <div
          className={
            this.props.value === this.props.content[1]
              ? 'Toggler togglerchange'
              : 'Toggler'
          }
          onClick={() => this.togglerchange(this.props.content[1])}
        >
          {this.props.content[1]}
        </div>
      </div>
    );
  }
}

Toggler.defaultProps = {
  content: ['YES', 'NO'],
};
