import React, { Component } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import Toggler from '../../../Atoms/Toggler';
import { alertAction } from '../../../../Store/commonActivity/alert/alert.action';
import { GoCalendar } from 'react-icons/go';

class SubsciptionConfig extends Component {
  sampleSubscription = {
    subscriptionDetails: {
      subscriberGroup: '',
      subscriptionInfo: {
        subscriptionStartDt: new Date(),
        subscriptionEndDt: new Date(),
        subscriptionDeactivate: '',
      },
    },
  };
  currentSelection = {};
  state = {
    selectedSubscriber: {},
    InteractionComponentData: this.props.InteractionComponentData,
  };
  visibleSubscription = currentSelection => {
    let InteractionComponentData = this.state.InteractionComponentData || [];
    let isNewSubscription = true;
    InteractionComponentData.forEach(val => {
      if (
        val.subscriptionDetails.subscriberGroup ===
        currentSelection.subscriberName
      ) {
        isNewSubscription = false;
        this.currentSelection = val;
      }
    });

    const subscriber = JSON.parse(JSON.stringify(this.sampleSubscription));
    if (isNewSubscription) {
      subscriber.subscriptionDetails.subscriberGroup =
        currentSelection.subscriberName;
      InteractionComponentData.push(subscriber);
      this.currentSelection = subscriber;
    }
    this.setState({
      selectedSubscriber: currentSelection,
      InteractionComponentData,
    });
  };

  changeHandler = (value, condition) => {
    if (!this.props.isEditPage) {
      return;
    }
    const currentSelection = this.currentSelection;
    if (condition === 'toggle') {
      if (
        currentSelection &&
        currentSelection.subscriptionDetails &&
        currentSelection.subscriptionDetails.subscriptionInfo &&
        currentSelection.subscriptionDetails.subscriptionInfo
          .subscriptionDeactivate
      ) {
        currentSelection['subscriptionDetails']['subscriptionInfo'][
          'subscriptionDeactivate'
        ] = value === 'ON' ? 'Y' : 'N';
      }
    }

    if (condition === 'startdate') {
      const selectedDate = new Date(value).setHours(0, 0, 0, 0);
      if (
        currentSelection &&
        currentSelection.subscriptionDetails &&
        currentSelection.subscriptionDetails.subscriptionInfo &&
        currentSelection.subscriptionDetails.subscriptionInfo.subscriptionEndDt
      ) {
        const endDate = new Date(
          currentSelection.subscriptionDetails.subscriptionInfo.subscriptionEndDt
        );
        endDate.setHours(0, 0, 0, 0);
        console.log(selectedDate, endDate, 'checkdate');
        if (selectedDate > endDate) {
          alertAction.openAlert({
            title: 'warning',
            text: 'start date must be lesser than end date',
          });
        } else {
          currentSelection['subscriptionDetails']['subscriptionInfo'][
            'subscriptionStartDt'
          ] = value;
        }
      }
    }

    if (condition === 'enddate') {
      if (
        currentSelection &&
        currentSelection.subscriptionDetails &&
        currentSelection.subscriptionDetails.subscriptionInfo &&
        currentSelection.subscriptionDetails.subscriptionInfo
          .subscriptionStartDt
      ) {
        const startDate = new Date(
          currentSelection.subscriptionDetails.subscriptionInfo.subscriptionStartDt
        );
        startDate.setHours(0, 0, 0, 0);
        if (value < startDate) {
          alertAction.openAlert({
            title: 'warning',
            text: 'end date must be greater than start date',
          });
        } else {
          currentSelection['subscriptionDetails']['subscriptionInfo'][
            'subscriptionEndDt'
          ] = value;
        }
      }
    }

    const InteractionComponentData = this.state.InteractionComponentData.map(
      val => {
        console.log(
          val.subscriptionDetails,
          currentSelection.subscriptionDetails,
          'checkSubsc'
        );
        if (
          val.subscriptionDetails.subscriberGroup ===
          currentSelection.subscriptionDetails.subscriberGroup
        ) {
          return currentSelection;
        }
        return val;
      }
    );
    console.log(InteractionComponentData, 'intcompdatass');
    this.setState({}, () => {
      this.props.callback &&
        this.props.callback({ ...this.props, InteractionComponentData });
    });
  };

  render() {
    const { Criteria = [] } = this.props;
    const currentSelection = this.currentSelection;
    const startDate =
      (currentSelection &&
        currentSelection.subscriptionDetails &&
        currentSelection.subscriptionDetails.subscriptionInfo &&
        currentSelection.subscriptionDetails.subscriptionInfo
          .subscriptionStartDt) ||
      new Date();
    const endDate =
      (currentSelection &&
        currentSelection.subscriptionDetails &&
        currentSelection.subscriptionDetails.subscriptionInfo &&
        currentSelection.subscriptionDetails.subscriptionInfo
          .subscriptionEndDt) ||
      new Date();

    const deactivate =
      (currentSelection &&
        currentSelection.subscriptionDetails &&
        currentSelection.subscriptionDetails.subscriptionInfo &&
        currentSelection.subscriptionDetails.subscriptionInfo
          .subscriptionDeactivate) ||
      '';

    const subscriberList = Array.isArray(this.props.AllSubscriptionList)
      ? this.props.AllSubscriptionList
      : [];

    return (
      <>
        <div className="subscription-config">
          <div className={`subscription-list `}>
            {subscriberList.map(val => {
              const name = (val && val.subscriberName) || '';
              return (
                <div
                  className={`subscription-group ${
                    this.state.selectedSubscriber.subscriberName ===
                    val.subscriberName
                      ? 'activeInstance'
                      : ''
                  }`}
                  onClick={() => this.visibleSubscription(val)}
                >
                  {name}
                </div>
              );
            })}
          </div>

          <div className="subscription-period-container">
            {this.state.selectedSubscriber.subscriberName && (
              <>
                <div className="Subscription-period">Subscription period</div>
                <div className="start">
                  <div className="title">Start Date</div>
                  <div className="date-picker-parent">
                    <DatePicker
                      selected={new Date(startDate)}
                      onChange={date => {
                        this.changeHandler(date, 'startdate');
                      }}
                      dateFormat="dd MMM yyyy"
                      className="date-picker"
                    />
                    <div className="calendar-icon">
                      <GoCalendar />
                    </div>
                  </div>
                </div>
                <div className="end">
                  <div className="title">Start Date</div>
                  <div className="date-picker-parent">
                    <DatePicker
                      selected={new Date(endDate)}
                      onChange={date => {
                        this.changeHandler(date, 'enddate');
                      }}
                      dateFormat="dd MMM yyyy"
                      className="date-picker"
                    />
                    <div className="calendar-icon">
                      <GoCalendar />
                    </div>
                  </div>
                </div>
                <div className="enable-subscription">
                  <div className="title">Subscription enabled</div>
                  <div className="toggle-subscription">
                    <Toggler
                      value={deactivate == 'N' ? 'OFF' : 'ON'}
                      onoffstatus={value => {
                        this.changeHandler(value, 'toggle');
                      }}
                      content={['ON', 'OFF']}
                      className="toggle-button"
                    />
                  </div>
                </div>
              </>
            )}
          </div>
        </div>
      </>
    );
  }
}

export default SubsciptionConfig;
