import React from 'react';
//import moment from 'moment';
import './addInstance.scss';
import SearchBox from '../../../Atoms/SearchBox';
import { businesseventConfiguration } from '../../../../Store/BusinessEventsPage/businessConfig.action';
import { connect } from 'react-redux';
import Button from '../../../Atoms/Button';
import upArrow from '../../../../../src/components/Img/upArrow.png';
//import EventListView from '../View-Instance/edit-instance';
import Criteria from '../Criteria/index';

class addInstance extends React.Component {
  state = {
    currentToggler: 'activeInstance',
    selectedInstance: {},
    searchBoxValue: '',
  };
  businesseventTemplateData = [];
  activeInstance = [];

  componentDidMount() {
    this.props.getBusinessEvent();
  }
  closeEdit = () => {
    this.setState({ selectedInstance: {} });
  };

  getInstance = instances => {
    this.activeInstance = [];
    instances.forEach(instance => {
      this.activeInstance.push(instance);
    });
  };

  searchHandler = (e, list) => {
    this.setState({ searchBoxValue: e.target.value, list });
  };

  selectInstance = instance => {
    if (
      this.state.selectedInstance.businessEventTemplateId ===
      instance.businessEventTemplateId
    ) {
      this.setState({ selectedInstance: {} });
    } else {
      const selectedInstance = JSON.parse(JSON.stringify(instance));
      this.setState({ selectedInstance });
    }
  };

  render() {
    if (this.props.businesseventTemplateData) {
      this.getInstance(this.props.businesseventTemplateData);
    }

    const content = {
      activeInstance: this.activeInstance,
    };
    let list = this.state.searchBoxValue.length
      ? this.state.list
      : content[this.state.currentToggler];
    return (
      <React.Fragment>
        <div className="BusinessEventInstances">
          <div className="BusinessEvents">
            <div className="BusinessEvent-left-box">
              <div className="BusinessEvent_Box">
                <div className="Business-searchContainer">
                  <div className="Business-Event-search-header">
                    <SearchBox
                      placeholder="Search by name"
                      onChange={this.searchHandler}
                      list={content[this.state.currentToggler]}
                      value={this.state.searchBoxValue}
                      className="Business-EventListSearchBar"
                      filterBy={['businessEventTemplateId', 'businessEvent']}
                    />
                  </div>
                </div>
              </div>
              <div className="Business-EventListBox scroll">
                {list &&
                  list.map((instance, index) => {
                    return (
                      <div
                        key={index}
                        className={
                          'Business-EventListDetails ' +
                          `${
                            this.state.selectedInstance
                              .businessEventTemplateId ===
                            instance.businessEventTemplateId
                              ? 'activeInstance'
                              : ''
                          }`
                        }
                        onClick={e => this.selectInstance(instance)}
                      >
                        <div className="BusinessListName-Area">
                          <div className="BusinessListID">
                            {instance.businessEventTemplateId}
                          </div>
                        </div>
                        <div className="BusinessListNameandBaseBody">
                          <div className="BusinessListName">
                            <div>{instance.businessEvent}</div>
                            <div className="BusinessListBaseBody">
                              {instance.eventDescription}
                            </div>
                          </div>
                        </div>
                        <div className="Business-Event-Transactional-button-Container">
                          <div className="Business-Event-Transactional-button-Area">
                            <Button className="Business-Event-Transactional-button">
                              {instance.eventType}
                            </Button>
                          </div>
                        </div>
                      </div>
                    );
                  })}
              </div>
            </div>
          </div>

          <div className="Selection-panel scroll">
            {this.state.selectedInstance.businessEventTemplateId ? (
              <Criteria
                key={Math.random()}
                instanceData={this.state.selectedInstance}
                enterpriseId={this.props.enterpriseId}
                enterpriseSignal={this.props.enterpriseSignal}
                SubscriberDetails={this.props.SubscriberDetails}
                closeEdit={this.closeEdit}
                page={'EDIT'}
              />
            ) : (
              <NoEvent />
            )}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

const NoEvent = () => {
  return (
    <div className="no-event">
      <div>
        <img src={upArrow} />
      </div>
      Select a Active Event Instances to view
    </div>
  );
};

function mapState(state) {
  return state.businesseventConfigReducer;
}

const actionCreators = {
  getBusinessEvent: businesseventConfiguration.getBusiness_Event_Template,
};

export default connect(mapState, actionCreators)(addInstance);
