// import { confirmPopup } from 'react-confirm-alert';
import React, { Component } from 'react';
import './popup.scss';
import Animate from '../addAnimation';
import Button from '../Button';

class PopupMax extends React.Component {
  render() {
    return (
      <Animate
        show={this.props.isOpen}
        className="PopupMax-bg-container"
        enterAnimate="zoom-out-enter .4s"
        leaveAnimate="zoom-out-leave .3s"
      >
        <div className="PopupMax-container">
          <div className="PopupMax-title">
            <b>{this.props.heading}</b>
          </div>
          <div className="PopupMax-message">{this.props.children}</div>
          <div className="PopupMax-control">
            <div className="buttonflex">
              <Button
                className="primary"
                onClick={e => {
                  if (this.props.clickHandler) {
                    this.props.clickHandler(e, 'save');
                  }
                }}
              >
                {this.props.buttonText}
              </Button>
              <Button
                className="primary"
                onClick={e => {
                  // console.log(this);

                  this.props.clickHandler(e, 'cancel');
                }}
              >
                Cancel
              </Button>
            </div>
          </div>
        </div>
      </Animate>
    );
  }
}

PopupMax.defaultProps = {
  isOpen: false,
  heading: 'Modal',
  buttonText: 'Save',
  clickHandler: () => {},
};

export default PopupMax;
