import React, { Component } from 'react';
import './edit-instance.scss';
import EventListViewDescription from './active-instance';
import './update-signal.scss';
import Criteria from '../Criteria';

class EventListView extends Component {
  state = { currentView: 'LIST' };

  handleEdit = currentView => {
    this.setState({ currentView });
  };

  cancelEdit = () => {
    this.setState({ currentView: 'LIST' });
    if (this.props.closeEdit) {
      this.props.closeEdit();
    }
  };

  render() {
    const instanceData = JSON.parse(JSON.stringify(this.props.instanceData));
    if (instanceData && !instanceData['schedulingCondition']) {
      instanceData['schedulingCondition'] = {
        scheduleKey: '',
        snoozesTimeValueMins: 0,
        snoozesTimeIncrementValueMins: 0,
        operator: 'ADD',
      };
    }

    return (
      <>
        {this.state.currentView === 'EDIT' ? (
          <Criteria
            instanceData={instanceData}
            page={'EDIT'}
            enterpriseId={this.props.enterpriseId}
            enterpriseSignal={this.props.enterpriseSignal}
            SubscriberDetails={this.props.SubscriberDetails}
            closeEdit={this.cancelEdit}
          />
        ) : (
          <div className="eventListView">
            <div className="userCreateMain">
              <div className="userCreateName">User Created Name</div>
              <div className="edit" onClick={() => this.handleEdit('EDIT')}>
                Edit
              </div>
            </div>
            <div className="bottomline"></div>

            <EventListViewDescription
              instanceData={this.props.instanceData}
              closeEdit={this.cancelEdit}
            />
          </div>
        )}
      </>
    );
  }
}

export default EventListView;
