import React, { Component } from 'react';

import Flight from '../Domain/flight';
import Pax from '../Domain/Pax';
import Crew from '../Domain/Crew';
import Bag from '../Domain/Crew';
import Engg from '../Domain/Engg';
import Ground from '../Domain/Engg';
import Button from '../../../Atoms/Button';
import { connect } from 'react-redux';
import { eventConfiguration } from '../../../../Store/EventConfigurationPage/eventConfig.action';
import Scheduling from '../Domain/Scheduling';
import SubsciptionConfig from '../Domain/Subscription';
import MappingSignal from '../Domain/mapping';

class UpdateSignal extends Component {
  removeSelection = () => {
    const criteria = ['entryCriteria', 'exitCriteria', 'reinitiateCriteria'];
    const instanceData = this.props.instanceData;
    criteria.forEach(val => {
      if (!instanceData[val]) return;
      let temp = [...instanceData[val]];
      temp = temp.map(data => {
        //check whether status is active or Draft
        delete data.selected;
      });
    });

    return instanceData;
  };

  state = {
    instanceData: this.removeSelection(),
    // originalData: JSON.parse(JSON.stringify(this.props.instanceData)),
  };

  ChooseTemplate = props => {
    const { active } = props;
    let Component = null;
    if (active.toLowerCase() === 'flight') {
      Component = <Flight {...props} />;
    }
    if (active.toLowerCase() === 'pax') {
      Component = <Pax {...props} />;
    }
    if (active.toLowerCase() === 'crew') {
      Component = <Crew {...props} />;
    }
    if (active.toLowerCase() === 'bag') {
      Component = <Bag {...props} />;
    }
    if (active.toLowerCase() === 'engg') {
      Component = <Engg {...props} />;
    }
    if (active.toLowerCase() === 'ground') {
      Component = <Ground {...props} />;
    }

    if (props.label === 'Subscribe') {
      Component = <SubsciptionConfig {...props} />;
    }

    if (Component) {
      return (
        <>
          {Component}
          {props.isEditPage && (
            <div className="save-container">
              <Button
                onClick={() => {
                  //    this.props.getEvent_Template();
                  this.props.closeEdit();
                }}
              >
                cancel
              </Button>
              <Button
                onClick={() => {
                  const criteria = [
                    'entryCriteria',
                    'exitCriteria',
                    'reinitiateCriteria',
                  ];
                  const instanceData = JSON.parse(
                    JSON.stringify(this.state.instanceData)
                  );
                  instanceData.mappingStatus = 'Active';
                  criteria.forEach(val => {
                    if (!instanceData[val]) return;
                    let temp = [...instanceData[val]];
                    temp = temp.map(data => {
                      //check whether status is active or Draft
                      if (data.metadata) {
                        Object.keys(data.metadata).forEach(key => {
                          if (!data.metadata[key]) {
                            instanceData.mappingStatus = 'Drafts';
                          }
                        });
                      }
                      delete data.selected;
                      delete data.isBESignalSelected;

                      return data;
                    });
                    instanceData[val] = temp;
                  });
                  delete instanceData.selectedIAOSignal;
                  delete instanceData.selectedEnterpriseSignal;
                  instanceData.enterpriseCode = this.props.enterpriseId;
                  this.props.updateInstance(instanceData);
                  this.props.closeEdit();
                }}
              >
                save
              </Button>
            </div>
          )}
        </>
      );
    }
    return null;
  };

  selectedData = data => {
    //,, this.setState({})

    const instanceData = { ...this.state.instanceData };

    // update condition for schedule page
    if (
      data.label === 'Schedule' &&
      Object.keys(data.InteractionComponentData).length
    ) {
      instanceData['schedulingCondition'] = data.InteractionComponentData;
    }

    //update condition for subscription page
    if (data.label === 'Subscribe' && instanceData['subscription']) {
      instanceData['subscription'] = data.InteractionComponentData;
    }

    //update condition for entry, exit , reinstate page
    if (data.label !== 'Subscribe' && data.label !== 'Schedule') {
      instanceData[data.CriteriaName].forEach(criteria => {
        if (data.isEnterpriseSelectedSignal) {
          // when we select enterprise signal
          if (criteria.iaoSignalName === instanceData.selectedIAOSignal) {
            criteria.namedSignal = data.selectedSignalName;
            instanceData.selectedEnterpriseSignal = data.selectedSignalName;
            criteria.isBESignalSelected = true;
            criteria.selected = [];
          }
        } else if (data.isUpdateForSignal) {
          // when we select iao signal
          if (criteria.iaoSignalName === data.currentSignal) {
            criteria.isBESignalSelected = true;
            instanceData.selectedIAOSignal = data.currentSignal;
            criteria.selected = [];
          }
        } else {
          //when we select iao signal attribute
          criteria.isBESignalSelected = false;
        }

        if (
          criteria.domain.toLowerCase() === data.domain &&
          criteria.iaoSignalName === data.signal
        ) {
          const metadata = criteria.metadata;
          // validate iao signal attribute selected or not
          if (metadata && typeof metadata === 'object') {
            Object.keys(metadata).forEach(meta => {
              if (meta === data.meta) {
                criteria.selected = [];
                instanceData.selectedEnterpriseSignal = criteria.namedSignal;
                criteria.selected.push(data.meta);
              }
            });
          }
        }
      });
    }

    this.setState({ instanceData });

    //instanceData[data.CriteriaName].forEach((val)=>{val.meta})
  };

  render() {
    const {
      entryCriteria = [],
      exitCriteria = [],
      reinitiateCriteria = [],
      schedulingCondition = {},
      subscription = [],
      eventType = '',
      selectedIAOSignal,
      selectedEnterpriseSignal,
    } = this.state.instanceData;

    const { label, active = '' } = this.props.tabData;
    const enterpriseId = this.props.enterpriseId;
    const isEditPage = this.props._props.isEditPage;
    const callback = this.props._props.isEditPage
      ? this.selectedData
      : () => {};

    const templateProps = {
      eventType,
      active,
      callback,
      isEditPage,
      label,
      enterpriseId,
    };

    if (label === 'Entry') {
      return this.ChooseTemplate({
        ...templateProps,
        Criteria: entryCriteria,
        InteractionComponent: MappingSignal,
        InteractionComponentData: {
          allSignal: this.props.enterpriseSignal,
          selectedEnterpriseSignal,
        },
        CriteriaName: 'entryCriteria',
      });
    }

    if (label === 'Exit') {
      return this.ChooseTemplate({
        ...templateProps,
        InteractionComponent: MappingSignal,
        InteractionComponentData: {
          allSignal: this.props.enterpriseSignal,
          selectedEnterpriseSignal,
        },
        Criteria: exitCriteria,
        CriteriaName: 'exitCriteria',
      });
    }

    if (label === 'Reinstate') {
      return this.ChooseTemplate({
        ...templateProps,
        InteractionComponent: MappingSignal,
        InteractionComponentData: {
          allSignal: this.props.enterpriseSignal,
          selectedEnterpriseSignal,
        },
        enterpriseList: this.props.EnterpriseDetails,
        Criteria: reinitiateCriteria,

        CriteriaName: 'reinitiateCriteria',
      });
    }

    if (label === 'Schedule') {
      return this.ChooseTemplate({
        ...templateProps,
        Criteria: entryCriteria,
        InteractionComponent: Scheduling,
        InteractionComponentData: schedulingCondition,
        CriteriaName: 'entryCriteria',
      });
    }
    if (label === 'Subscribe') {
      return this.ChooseTemplate({
        ...templateProps,
        InteractionComponentData: subscription,
        AllSubscriptionList: this.props.SubscriberDetails,
        CriteriaName: 'Subscribe',
      });
    }

    return null;
  }
}
const mapStateToProps = state => {
  return {
    mappingData: state.eventConfigReducer.mappingData,
  };
};

const actionCreator = {
  updateInstance: eventConfiguration.updateInstance,
  getEvent_Template: eventConfiguration.getEvent_Template,
};

export default connect(mapStateToProps, actionCreator)(UpdateSignal);
