import './accordionSingle.scss';
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
class Panel extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      height: 0,
    };
    this.clearTimer = 0;
  }

  componentDidMount() {
    this.clearTimer = window.setTimeout(() => {
      const el = this && ReactDOM.findDOMNode(this);
      if (el && el.querySelector('.panel__inner')) {
        const height = el.querySelector('.panel__inner').scrollHeight;
        this.setState({
          height,
        });
      }
    }, 333);
  }

  componentWillUnmount() {
    clearTimeout(this.clearTimer);
  }

  render() {
    const { title, data, activeTab, index, activateTab } = this.props;
    const Data = data;

    const { height } = this.state;
    const isActive = activeTab === index;
    const innerStyle = {
      height: `${isActive ? height : 0}px`,
    };

    return (
      <div className="panel" role="tabpanel" aria-expanded={isActive}>
        <button className="panel__label" role="tab" onClick={activateTab}>
          {title}
        </button>
        <div
          className="panel__inner"
          style={innerStyle}
          aria-hidden={!isActive}
        >
          <p className="panel__content">{<Data />}</p>
        </div>
      </div>
    );
  }
}

export default class AccordionSingle extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      activeTab: props.activateTab,
    };

    this.activateTab = this.activateTab.bind(this);
  }

  activateTab(index) {
    this.setState(prev => ({
      activeTab: prev.activeTab === index ? -1 : index,
    }));
  }

  render() {
    const { menuList } = this.props;
    const { activeTab } = this.state;

    return (
      <div className="accordion" role="tablist">
        {menuList.map((panel, index) => (
          <Panel
            key={index}
            activeTab={activeTab}
            index={index}
            {...panel}
            activateTab={this.activateTab.bind(null, index)}
          />
        ))}
      </div>
    );
  }
}

AccordionSingle.defaultProps = {
  activateTab: 0,
};
