import React, { Component } from 'react';
import Tabs from '../../Atoms/tab/index';
import TabsWithBorder from '../../Atoms/tab-with-border/tab';

class MultiTabs extends Component {
  state = { tab: this.props.tabData };
  render() {
    return (
      <Tabs>
        {this.state.tab.map((Outer, index) => {
          return (
            <div
              key={index}
              label={Outer.label}
              className={this.props.className}
            >
              <TabsWithBorder
                active={Outer.active}
                className={this.props.className}
                onChange={active => {
                  const data = [...this.state.tab];
                  this.state.tab.forEach((val, index) => {
                    if (val.label === Outer.label) {
                      data[index].active = active;
                    }
                  });
                  this.setState({ tab: data });
                }}
              >
                {Outer.childtab.map(innerTitle => {
                  return <div key={innerTitle}>{innerTitle}</div>;
                })}
              </TabsWithBorder>

              {
                <Outer.Component
                  key={Math.random()}
                  tabData={Outer}
                  _props={this.props}
                />
              }
            </div>
          );
        })}
      </Tabs>
    );
  }
}
MultiTabs.defaultProps = {
  className: '',
  tabData: [],
};

export default MultiTabs;
