import React, { Component, createRef } from 'react';
import AccordionSingle from '../../../Atoms/Accordion/accordionSingle';
import { connect } from 'react-redux';
import service from '../../../../services/service';
import environment from '../../../..';
import Slide from '../../../Atoms/slidedown';
import Accordion from '../../../Atoms/Accordion';
import '../index.scss';
import { MdCheck } from 'react-icons/md';

class SelectSignal extends Component {
  signals = [
    <div
      className="signal"
      onClick={() => {
        this.props.callback({
          ...this.props.data,
          isEnterpriseSelectedSignal: true,
          selectedSignalName: 'ON',
        });
      }}
    >
      {'ON'}
    </div>,
    <div
      className="signal"
      onClick={() => {
        this.props.callback({
          ...this.props.data,
          isEnterpriseSelectedSignal: true,

          selectedSignalName: 'OFF',
        });
      }}
    >
      {'OFF'}
    </div>,
  ];
  menuList = [];
  componentDidMount() {
    this.generateList();
  }
  generateList = () => {
    service
      .get(
        environment.EnterpriceConfig.SignalDetails +
          this.props.data.enterpriseId +
          '/signal'
      )
      .then(response => {
        console.log(response, this.props, 'respo data');
        if (response && response.data && Array.isArray(response.data)) {
          const activeElement = createRef();
          const currentSignalDomain =
            this.props.data.active && this.props.data.active.toLowerCase();
          const isActive = (this.signals = response.data.map(val => {
            if (val.signalDomain.toLowerCase() === currentSignalDomain) {
              return (
                <div
                  className="signal"
                  onClick={() => {
                    this.props.callback({
                      ...this.props.data,
                      isEnterpriseSelectedSignal: true,
                      selectedSignalName: val.signalName,
                    });
                  }}
                >
                  {val.signalName}{' '}
                  {val.selected && (<span><MdCheck/></span>)}
                  
                </div>
              );
            }
            return null;
          }));
        }
        this.menuList = [
          {
            title: this.props.data.enterpriseId,
            isOpen: true,
            data: () => this.signals,
          },
        ];

        this.setState({});
      });
  };

  render() {
    return <Accordion className="mapping-signal" menuList={this.menuList} />;
  }
}

export default SelectSignal;
