import React, { Component, createRef } from 'react';
import Slide from '../../../Atoms/slidedown';
import './mapping.scss';
import { FaChevronUp, FaChevronDown } from 'react-icons/fa';
import { MdCheck } from 'react-icons/md';

class MappingSignal extends Component {
  state = {};
  activeElement = createRef();
  timeout = 0;
  mappingList = {};
  componentDidMount() {
    this.scrollingToPosition();
  }

  scrollingToPosition = () => {
    const node = this.activeElement.current;
    if (node) {
      this.timeout = setTimeout(() => {
        node.scrollIntoView({
          behavior: 'smooth',
          block: 'center',
          inline: 'center',
        });
      }, 900);
    }
  };

  componentWillUnmount() {
    clearTimeout(this.timeout);
  }

  componentDidUpdate() {
    this.scrollingToPosition();
  }

  generateList = (data, newObj) => {
    if (typeof data === 'object')
      Object.keys(data).forEach(val => {
        if (typeof data[val] === 'object') {
          if (data[val]['properties']) {
            const temp = JSON.parse(JSON.stringify(data[val]['properties']));
            newObj[val] = temp;
            const path = data.currentListPath
              ? data.currentListPath + '.' + val
              : val;
            newObj[val].currentListPath = path;
            this.generateList(temp, newObj[val]);
          } else if (typeof data === 'object' && data[val]) {
            newObj[val] = data[val];
            const path = data.currentListPath
              ? data.currentListPath + '.' + val
              : val;
            newObj[val].currentListPath = path;
            this.generateList(data[val], newObj[val]);
          }
        }
      });
  };

  toggleCreation = mapping => {
    const criteria = this.props.data.Criteria[0];
    const selectedSignal = criteria['selected'][0];
    const currentAttribute = criteria.metadata[selectedSignal];
    if (typeof mapping === 'object') {
      return Object.keys(mapping).map((data, i) => {
        if (typeof mapping[data] === 'object' && mapping.hasOwnProperty(data)) {
          const contentData = this.toggleCreation(
            JSON.parse(JSON.stringify(mapping[data]))
          );

          const path = mapping.currentListPath
            ? mapping.currentListPath + '.' + data
            : data;
          // if data not available then this is last child
          if (!contentData[0]) {
            // return null;
            const isActive = currentAttribute === path ? true : false;
            return (
              <div
                key={i}
                className={`last-child-mapper`}
                onClick={e => {
                  criteria.metadata[selectedSignal] = path;

                  if (this.props.callback) {
                    this.props.callback(this.props.data);
                  }
                }}
              >
                <div>
                  {data}
                  {isActive && (
                    <div ref={this.activeElement} className="mapping-tick">
                      <MdCheck />
                    </div>
                  )}
                </div>
              </div>
            );
          }
          const isopen = currentAttribute.includes(path) ? true : false;
          return (
            <div className="mappingslider">
              <Slide
                key={isopen ? i : Math.random()}
                open={isopen}
                header={data}
                content={contentData}
                customToggler={{
                  open: <FaChevronUp />,
                  close: <FaChevronDown />,
                }}
              />
            </div>
          );
        } else {
          return null;
        }
      });
    }
    return null;
  };

  render() {
    const {
      allSignal = [],
      selectedEnterpriseSignal,
    } = this.props.data.InteractionComponentData;
    if (!selectedEnterpriseSignal) {
      return (
        <div className="error-text-mapping">
          {' '}
          Please Select Enterprise Signal
        </div>
      );
    }
    let ComponentData;
    allSignal.forEach(val => {
      if (val.signalName === selectedEnterpriseSignal) {
        ComponentData = val.signalContent;
      }
    });

    if (!ComponentData) {
      return (
        <div className="error-text-mapping">
          {' '}
          No Mapping File for this Signal{' '}
        </div>
      );
    }
    const mappingData =
      (ComponentData &&
        ComponentData.sourcemessage &&
        ComponentData.sourcemessage.mappings &&
        ComponentData.sourcemessage.mappings.properties) ||
      {};
    this.generateList(mappingData, this.mappingList);

    return (
      <div className="mapping-parent">
        {this.toggleCreation(this.mappingList)}
      </div>
    );
  }
}

export default MappingSignal;
