import React, { Component } from 'react';
import { Dropdown } from '../../../Atoms/DropdownComponent/index';
import InputElement from '../../../Atoms/InputElement';
class Scheduling extends Component {
  state = {
    snoozeTime:
      this.props.data.InteractionComponentData.snoozesTimeValueMins || 0,
    IncrementValue:
      this.props.data.InteractionComponentData.snoozesTimeIncrementValueMins ||
      0,
    list: [
      { title: 'ADD', selected: true },
      { title: 'SUBTRACT', selected: false },
    ],
    title: this.props.data.InteractionComponentData.operator,
    data: this.props.data.InteractionComponentData,
  };

  changeInput = (e, val) => {
    if (!this.props.data.isEditPage) {
      return;
    }
    const input = e.target.value;
    if (/^\d*$/.test(input))
      this.setState({ [val]: input }, () => {
        this.props.callback({
          ...this.props.data,
          ...this.createInteractionComponentData(),
        });
      });
  };

  createInteractionComponentData = () => {
    let operator = '';
    this.state.list.forEach(val => {
      if (val.selected) {
        operator = val.title;
      }
    });
    const InteractionComponentData = this.props.data.InteractionComponentData;
    InteractionComponentData.snoozesTimeValueMins = this.state.snoozeTime;
    InteractionComponentData.snoozesTimeIncrementValueMins = this.state.IncrementValue;

    InteractionComponentData.operator = operator;
    return InteractionComponentData;
  };

  changeOperator = item => {
    if (!this.props.data.isEditPage) {
      return;
    }
    const { list } = this.state;
    list.forEach(val => {
      val.selected = false;
      if (val.title === item.title) {
        val = item;
        val.selected = true;
      }
    });
    this.setState({ list }, () => {
      this.props.callback({
        ...this.props.data,
        ...this.createInteractionComponentData(),
      });
    });
  };

  render() {
    if (!Object.keys(this.props.data.InteractionComponentData).length)
      return null;
    return (
      <div className="Scheduling">
        <div className="scheduling-condition">
          <div className="condition">Condition </div>
          <div className="Operator">
            <div className="Operator-title">Operator</div>

            <Dropdown
              dropdownDisable={this.props.data.isEditPage ? false : true}
              title={this.props.data.InteractionComponentData.operator}
              list={this.state.list}
              toggleItem={this.changeOperator}
            />
          </div>
          <div className="schedule-time">
            <div className="snooze">
              <div className="snooze-title">Snooze time</div>
              <div className="scheduletimeinput">
                <InputElement
                  value={this.state.snoozeTime}
                  type={'text'}
                  onChange={e => this.changeInput(e, 'snoozeTime')}
                />
                <span className="minute">Min</span>
              </div>
            </div>
            <div className="incremental">
              <div className="incremental-title">Incremental Value</div>
              <div className="scheduletimeinput">
                <InputElement
                  value={this.state.IncrementValue}
                  type={'text'}
                  onChange={e => this.changeInput(e, 'IncrementValue')}
                />
                <span className="minute">Min</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Scheduling;
