import React, { Component, useRef, useEffect, useState } from 'react';
import EventList from './View-Instance/instances-list';
import Button from '../../Atoms/Button';
import { GoPlus, GoListUnordered } from 'react-icons/go';
import './index.scss';
import AddInstance from './Add-Instance/addInstance';
import { connect } from 'react-redux';
import { Enterprise_Config } from '../../../Store/Enterprise_Config/action';
import { MdCheck } from 'react-icons/md';

class EventConfiguration extends Component {
  header = {
    ActiveEventInstanceHeader: {
      Component: EventList,
      headerName: 'Active Event Instances',
      buttonName: 'Add Instance',
      navigateTo: 'AddInstanceHeader',
      Icon: GoPlus,
    },
    AddInstanceHeader: {
      headerName: 'Add Instance',
      Component: AddInstance,
      buttonName: 'View Instance',
      navigateTo: 'ActiveEventInstanceHeader',
      Icon: GoListUnordered,
    },
  };

  state = {
    currentPage: this.header.ActiveEventInstanceHeader,
    isEnterpriseListScreen: true,
    selectedEnterPrise: '',
  };
  componentDidMount() {
    this.props.getEnterpriseList();
    this.props.getSubscriber();
  }

  chooseEnterprise = val => {
    this.props.getSignalDetails(val.enterpriseId);
    this.setState({
      isEnterpriseListScreen: false,
      selectedEnterPrise: val.enterpriseId,
    });
  };

  navigate = e => {
    Object.keys(this.header).forEach(val => {
      if (e.target.innerText === this.header[val].buttonName) {
        const currentPage = this.header[val].navigateTo;
        this.setState({ currentPage: this.header[currentPage] });
      }
    });
  };
  render() {
    const { Icon, Component, buttonName, headerName } = this.state.currentPage;
    const { EnterpriseList = [] } = this.props;
    if (this.state.isEnterpriseListScreen) {
      return (
        <ChooseEnterpriseList
          EnterpriseList={EnterpriseList}
          chooseEnterprise={this.chooseEnterprise}
        />
      );
    }

    return (
      <div className="main">
        <div className="Events">
          <div className="EventManagementHeader">
            <div className="EventManagementwrap">
              <div
                className="EventManagementText"
                onClick={() => {
                  this.setState({ isEnterpriseListScreen: true });
                }}
              >
                {'Select Enterprise'}
              </div>
              <div className="Event-header-right-content">
                <div className="event-header-create">
                  <Icon className="event-create-icon" />
                  <Button
                    className="CreateEventTemplate"
                    onClick={this.navigate}
                  >
                    {buttonName}
                  </Button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="config-container">
          {
            <Component
              enterpriseId={this.state.selectedEnterPrise}
              enterpriseSignal={this.props.allSignals}
              SubscriberDetails={this.props.SubscriberDetails}
              EnterpriseList={this.props.EnterpriseList}
            />
          }
        </div>
      </div>
    );
  }
}

export const ChooseEnterpriseList = ({ EnterpriseList, chooseEnterprise }) => {
  const [selectedEnterPrise, setSelectedEnterPrise] = useState({});
  return (
    <div className="main">
      <div className="choose-enterprise-heading">
        Select a Enterprise to View, Add, Edit Instances
      </div>
      <div className="choose-enterprise-list-container scroll">
        {EnterpriseList.map(val => {
          return (
            <div
              className="list-enterprise"
              onClick={() => {
                chooseEnterprise(val);
                setSelectedEnterPrise(val);
              }}
            >
              <div className="enterprise-logo">
                <div className="logo-img">
                  {val.enterpriseId && val.enterpriseId[0]}
                </div>
              </div>
              {/* {selectedEnterPrise.enterpriseId === val.enterpriseId && (
                <div className="mapping-tick">
                  <MdCheck />
                </div>
              )} */}
              <div className="enterprise-details">
                <div className="id-name-container">
                  <div className="enterprise-id">{val.enterpriseId}</div>
                  <div className="enterprise-name">{val.enterpriseName}</div>
                </div>
                <div className="enterprise-desc">
                  {val.enterpriseDescription}
                </div>
              </div>
              {selectedEnterPrise.enterpriseId === val.enterpriseId && (
                <div className="mapping-tick">
                  <MdCheck />
                </div>
              )}
            </div>
          );
        })}
      </div>
    </div>
  );
};

const mapDispatchToProps = {
  getEnterpriseList: Enterprise_Config.getEnterpriseDetails,
  getSignalDetails: Enterprise_Config.getSignalDetail,
  getSubscriber: Enterprise_Config.get_subscriber,
};
const mapStateToProps = state => {
  return {
    EnterpriseList: state.EnterpriceReducer,
    allSignals: state.EnterpriseSignal,
    SubscriberDetails: state.SubscriberReducer,
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(EventConfiguration);
