import React, { useState } from 'react';
import Accordion from '../../../Atoms/Accordion';
import SelectSignal from './selectSignal';

const Pax = props => {
  const {
    Criteria = [],
    callback = () => {},
    CriteriaName = '',
    isEditPage,
    InteractionComponent = () => null,
    InteractionComponentData = '',
    label = '',
    eventType = '',
    enterpriseId = '',
  } = props;
  let count = 0;
  const currentType = eventType.toLowerCase();
  const mapData = new Map();
  mapData.set('transactional', ['scheduling', 'reinitiate']);
  mapData.set('stateless', ['exitcriteria']);
  mapData.set('temporal', []);
  mapData.set('passthrough', ['exitcriteria']);
  mapData.set('stateful', []);
  const isPageShow =
    mapData.get(currentType) && mapData.get(currentType).includes(label)
      ? false
      : true;

  // checking whether attribute selected
  let isAttrSelected = false;

  //check whether business event signal selected
  let isBESignalSelected = false;

  //click handler for business event signal
  const SignalHandler = (value, status) => {
    const signalname = value.iaoSignalName;
    if (status) {
      isBESignalSelected = true;
      isAttrSelected = false;
      callback({
        ...props,
        isUpdateForSignal: true,
        currentSignal: signalname,
      });
    }
  };

  //generate the UI for business event signal list and attr list
  const UI = Criteria.map((entryData, index) => {
    const domain = (entryData.domain && entryData.domain.toLowerCase()) || '';
    if (entryData.isBESignalSelected) {
      isBESignalSelected = true;
    }
    if (domain === 'pax' && isPageShow) {
      count++;
    }

    const showEndDigit = (text = '') => {
      if (isEditPage) {
        if (text.length > 50) {
          return `... ${text.slice(-50)}`;
        }
      } else {
        if (text.length > 100) {
          return `... ${text.slice(-100)}`;
        }
      }

      return text;
    };
    let isopen = false;
    if (label === 'Schedule') {
      if (entryData.iaoSignalName) {
        Object.keys(entryData.metadata).forEach(meta => {
          if (meta === InteractionComponentData.scheduleKey) {
            isopen = true;
          }
        });
      }
    }

    if (label !== 'Schedule') {
      if (entryData.selected && entryData.selected.length) {
        isAttrSelected = true;
      }
      if (isAttrSelected) {
        isopen = true;
      }
    }
    return (
      domain === 'pax' && (
        <div key={index}>
          <div className="event-signals scroll">
            <Accordion
              onClick={(value, status) => SignalHandler(entryData, status)}
              menuList={[
                {
                  title: (
                    <div>
                      <div>{entryData.iaoSignalName}</div>
                      {entryData.namedSignal && (
                        <div className="selected-signal">
                          {isEditPage ? 'Selected' : 'Mapped Enterprise'}{' '}
                          Signal: {entryData.namedSignal}
                        </div>
                      )}
                    </div>
                  ),
                  isOpen: isopen,
                  data: () => (
                    <div
                      className={`signal-attribute `}
                      style={{ position: 'relative' }}
                    >
                      {Object(entryData.metadata) === entryData.metadata &&
                        Object.keys(entryData.metadata).map((meta, i) => {
                          if (label === 'Schedule') {
                            const active =
                              (InteractionComponentData &&
                                InteractionComponentData.scheduleKey) ||
                              '';
                            return (
                              <div
                                data-tip="React-tooltip"
                                key={i}
                                onClick={() => {
                                  let data = {
                                    scheduleKey: '',
                                    snoozesTimeValueMins: 0,
                                    snoozesTimeIncrementValueMins: 0,
                                    operator: 'ADD',
                                  };
                                  if (active) {
                                    if (
                                      meta ===
                                      InteractionComponentData.scheduleKey
                                    ) {
                                      data = {
                                        ...data,
                                        ...InteractionComponentData,
                                      };
                                    }
                                  }
                                  data.scheduleKey = meta;
                                  InteractionComponentData.scheduleKey = meta;

                                  callback({
                                    ...props,
                                    InteractionComponentData: data,
                                  });
                                }}
                                className={meta === active ? 'active' : ''}
                              >
                                <h3>{meta}</h3>

                                <p>{showEndDigit(entryData.metadata[meta])}</p>
                              </div>
                            );
                          } else
                            return (
                              <div
                                data-tip="React-tooltip"
                                key={i}
                                onClick={() => {
                                  callback({
                                    ...props,
                                    domain,
                                    signal: entryData.iaoSignalName,
                                    meta,
                                    makeUpdateFromParent: true,
                                  });
                                }}
                                className={
                                  entryData.selected &&
                                  isEditPage &&
                                  entryData.selected.includes(meta)
                                    ? 'active'
                                    : ''
                                }
                              >
                                <h3>{meta}</h3>

                                <p>{showEndDigit(entryData.metadata[meta])}</p>
                              </div>
                            );
                        })}
                    </div>
                  ),
                },
              ]}
            />
          </div>
        </div>
      )
    );
  });
  if (count === 0) {
    return (
      <div
        style={{
          color: 'rgb(75, 80, 195)',

          padding: '20px',
          display: 'flex',
          justifyContent: 'center',
          boxShadow: 'grey 3px 0px 10px -5px',
          marginTop: '10px',
          marginBottom: '6px',
          borderRadius: '14px',
          boxShadow: '0 0 4px 0px gray',
        }}
      >
        Not configurable or no data available
      </div>
    );
  }

  return (
    <div className="scroll config">
      <div
        className="update-event-config scroll"
        style={!isEditPage ? { flex: 1 } : {}}
      >
        {' '}
        {UI}
      </div>
      {isEditPage && (
        <div className="event-signal-value scroll">
          {label === 'Schedule' && (
            <InteractionComponent callback={callback} data={props} />
          )}
          {label !== 'Schedule' && isAttrSelected && (
            <InteractionComponent callback={callback} data={props} />
          )}

          {label !== 'Schedule' && isBESignalSelected && (
            <SelectSignal callback={callback} data={props} />
          )}
        </div>
      )}
      {!isEditPage && label === 'Schedule' && (
        <div className="event-signal-value">
          <InteractionComponent callback={callback} data={props} />
        </div>
      )}
    </div>
  );
};

export default Pax;
