import React, { useState, useEffect } from 'react';
import '../Accordion/Accordion.scss';
import { FaAngleDown, FaAngleUp } from 'react-icons/fa';
import Slide from '../slidedown';

class Accordion extends React.Component {
  state = {};

  callback = (value, status) => {
    this.setState({});
    this.props.onClick(value, status);
  };

  headerComponent = header => {
    return (
      <div className="heading">
        <label className="tab-label">
          {header}
          <span>
            {' '}
            <span className="minus">
              <FaAngleUp />
            </span>
            <span className="add">
              <FaAngleDown />
            </span>
          </span>
        </label>
      </div>
    );
  };

  contentComponent = Content => {
    return <div className="tab-content">{Content}</div>;
  };

  render() {
    return this.props.menuList.map((value, i) => (
      <div
        className="row"
        key={this.props.isSingleOpen ? Math.random() * i : i}
      >
        <div className="tabs">
          <div className="tab">
            <Slide
              sliderStatus={status => this.callback(value, status)}
              open={value.isOpen ? true : false}
              header={this.headerComponent(value.title)}
              content={this.contentComponent(<value.data />)}
              {...this.props}
            />
          </div>
        </div>
      </div>
    ));
  }
}

Accordion.defaultProps = {
  onClick: () => {},
  isSingleOpen: false,
};

export default Accordion;
