import React from 'react';
import moment from 'moment';
import './instances-list.scss';
import Button from '../../../Atoms/Button';
import TabsWithBorder from '../../../Atoms/tab-with-border/tab';
import { eventConfiguration } from '../../../../Store/EventConfigurationPage/eventConfig.action';
import { connect } from 'react-redux';
import upArrow from '../../../../../src/components/Img/upArrow.png';
import SearchBox from '../../../Atoms/SearchBox';
import EventListView from './edit-instance';
import HoverContainer from '../../../Atoms/hoverContainer';
import Popup from '../../../Atoms/popup/popup';
import { ChooseEnterpriseList } from '..';
import PopupMax from '../../../Atoms/popup/popupMax';

class EventList extends React.Component {
  state = {
    currentToggler: 'activeInstance',
    selectedInstance: {},
    searchBoxValue: '',
    list: [],
    isCloneSelectEnterprisePopUpOpened: false,
    selectedClone: {},
    selectedCloneEnterprise: {},
  };
  activeInstance = [];
  DraftsInstance = [];

  componentDidMount() {
    this.props.getEvent();
  }

  date = value => {
    let val = value != undefined ? value : '';
    var now = new Date(val);
    var dateString = moment(now).format('DD MMM YY ');
    return dateString.toUpperCase();
  };

  time = value => {
    let val = value != undefined ? value : '';
    var str = val.slice(0, -1);
    var now = new Date(str);
    var timestring = moment(now).format('HH:mm');
    return timestring;
  };

  getInstance = instances => {
    this.activeInstance = [];
    this.DraftsInstance = [];
    instances.forEach(instance => {
      if (this.props.enterpriseId === instance.enterpriseCode) {
        if (
          instance.mappingStatus &&
          instance.mappingStatus.toLowerCase() === 'active'
        ) {
          this.activeInstance.push(instance);
        } else if (
          instance.mappingStatus &&
          instance.mappingStatus.toLowerCase() === 'drafts'
        ) {
          this.DraftsInstance.push(instance);
        } 
      }
    });
  };

  selectInstance = instance => {
    if (
      this.state.selectedInstance.businessEventId === instance.businessEventId
    ) {
      this.setState({ selectedInstance: {} });
    } else {
      const selectedInstance = instance;
      this.setState({ selectedInstance });
    }
  };

  instanceData = data => {
    let tempData = {};
    if (data && data.businessEventId) {
      const instanceData = this.props.eventTemplateData;
      if (instanceData) {
        instanceData.forEach(val => {
          if (val.businessEventId === data.businessEventId) {
            tempData = val;
          }
        });
      }
    }
    return tempData;
  };

  cloneInstance = (e, instance) => {
    e.stopPropagation();
    this.setState({
      isCloneSelectEnterprisePopUpOpened: true,
      selectedClone: instance,
    });
  };

  searchHandler = (e, list) => {
    this.setState({ searchBoxValue: e.target.value, list });
  };

  updateClone = (e, text) => {
    if (text === 'save') {
      const { selectedCloneEnterprise, selectedClone } = this.state;
      if (selectedClone.businessEventId) {
        selectedClone.businessEventId = '';
        selectedClone.enterpriseCode = selectedCloneEnterprise.enterpriseId;
        this.props.updateInstance(selectedClone);
      }
    }
    this.setState({
      isCloneSelectEnterprisePopUpOpened: false,
      selectedClone: {},
    });
  };

  render() {
    if (this.props.eventTemplateData) {
      this.getInstance(this.props.eventTemplateData);
    }
    const content = {
      activeInstance: this.activeInstance,
      draftInstance: this.DraftsInstance,
    };

    let list = this.state.searchBoxValue.length
      ? this.state.list
      : content[this.state.currentToggler];

    if (this.state.isCloneSelectEnterprisePopUpOpened) {
      return (
        <PopupMax
          isOpen={true}
          clickHandler={(e, text) => {
            this.updateClone(e, text);
          }}
          heading={'Select Enterprise for Clone Instance'}
        >
          <ChooseEnterpriseList
            EnterpriseList={this.props.EnterpriseList}
            chooseEnterprise={enterprise => {
              this.setState({ selectedCloneEnterprise: enterprise });
            }}
          />
        </PopupMax>
      );
    }
    return (
      <React.Fragment>
        <div className="ActiveEventInstances">
          <div className="event-list">
            <div className="Event-left-box">
              <div className="Event_Box">
                <div className="searchContainer">
                  <div className="Event-search-header">
                    <SearchBox
                      placeholder="Search by name"
                      onChange={this.searchHandler}
                      list={content[this.state.currentToggler]}
                      value={this.state.searchBoxValue}
                      className="EventListSearchBar"
                      filterBy={['businessEventId', 'businessEvent']}
                    />
                  </div>
                </div>
              </div>
              <div className="EventListBox scroll">
                <div className="EventListHeaderBox">
                  <TabsWithBorder
                    active={this.state.currentToggler}
                    onChange={currentToggler =>
                      this.setState({ currentToggler, selectedInstance: {} })
                    }
                  >
                    <div key="activeInstance" className="EventListHeader">
                      <b> {`Active(${this.activeInstance.length})`}</b>
                    </div>
                    <div key="draftInstance" className="EventListHeader">
                      <b> {`Drafts(${this.DraftsInstance.length})`}</b>
                    </div>
                  </TabsWithBorder>
                </div>
                {list.map((instance, index) => {
                  return (
                    <div
                      key={index}
                      className={
                        'EventListDetails ' +
                        `${
                          this.state.selectedInstance.businessEventId ===
                          instance.businessEventId
                            ? 'activeInstance'
                            : ''
                        }`
                      }
                      onClick={e => this.selectInstance(instance)}
                    >
                      <HoverContainer
                        isEditIconShow={false}
                        isDeleteIconShow={false}
                        isCloneIconShow={true}
                        cloneHandler={e => {
                          this.cloneInstance(e, instance);
                        }}
                      >
                        <div className="EventListName">
                          {instance.businessEventId}
                        </div>
                        {/* <div className="EventListUserName">
                        User Created Name User Created Name
                      </div> */}
                        <div className="EventListBEventName">
                          {instance.businessEvent}
                        </div>
                        <div className="EventListULName">
                          {/* <div className="user-info">12:30 | 19 JUN 20</div> */}
                          <div className="Event-Transactional-button-Area">
                            <Button className="Event-Transactional-button">
                              {instance.eventType}
                            </Button>
                          </div>
                        </div>
                      </HoverContainer>
                    </div>
                  );
                })}
              </div>
            </div>
          </div>
          <div className="Selection-panel scroll">
            {this.state.selectedInstance.businessEventId ? (
              <EventListView
                key={Math.random()}
                instanceData={this.instanceData(this.state.selectedInstance)}
                closeEdit={this.props.closeEdit}
                enterpriseId={this.props.enterpriseId}
                enterpriseSignal={this.props.enterpriseSignal}
                SubscriberDetails={this.props.SubscriberDetails}
              />
            ) : (
              <NoEvent />
            )}
          </div>
        </div>
      </React.Fragment>
    );
  }
}

const NoEvent = () => {
  return (
    <div className="no-event">
      <div>
        <img src={upArrow} />
      </div>
      Select a Active Event Instances to view
    </div>
  );
};

function mapState(state) {
  return state.eventConfigReducer;
}

const actionCreators = {
  getEvent: eventConfiguration.getEvent_Template,
  updateInstance: eventConfiguration.updateInstance,
};

export default connect(mapState, actionCreators)(EventList);
