import React, { Component } from 'react';
import './edit-instance.scss';
import Criteria from '../Criteria';

class EventListViewDescription extends Component {
  state = {};

  render() {
    const {
      businessEventTemplateId,
      businessEvent,
      eventDescription,
      influencers = [],
      businessTargetedDomains = [],
      ruleMapping = [],
      subscription = [],
    } = this.props.instanceData;

    return (
      <>
        <div className="eventListViewDescription">
          <div className="businesseventname">
            <div className="businesseventdetails">
              <div className="eventNo">{businessEventTemplateId}</div>
              <div className="eventName">{businessEvent}</div>
            </div>
            <div className="businesseventdescription">{eventDescription}</div>
          </div>
          <Subscribers subscription={subscription} />
          <CorrelatdDomain businessTargetedDomains={businessTargetedDomains} />
          <InfluencedEnterpriseEvents influencers={influencers} />
          <DeterminantRules ruleMapping={ruleMapping} />
          <Creteria
            instanceData={this.props.instanceData}
            closeEdit={this.props.closeEdit}
          />
        </div>
      </>
    );
  }
}

export default EventListViewDescription;

const Creteria = ({ instanceData = [], closeEdit }) => {
  return (
    <div>
      <div className="Domain">
        <div className="header">Conditions</div>
        <Criteria
          instanceData={instanceData}
          page={'VIEW'}
          closeEdit={closeEdit}
        />
      </div>
    </div>
  );
};

export const Subscribers = ({ subscription }) => {
  if (!subscription.length) return null;
  return (
    <>
      <div className="Domain">
        <div className="header">Subscribers</div>
        <div className="domainDetails">
          {subscription.map((subscribe, index) => {
            return (
              <div key={index} className="domainNames">
                {subscribe.subscriptionDetails.subscriberGroup}
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};
export const CorrelatdDomain = ({ businessTargetedDomains }) => {
  return (
    <>
      <div className="Domain">
        <div className="header">Correlated Domain</div>
        <div className="domainDetails">
          {businessTargetedDomains.map((val, index) => (
            <div key={index} className="domainNames">
              {val}
            </div>
          ))}
        </div>
      </div>
    </>
  );
};

export const InfluencedEnterpriseEvents = ({ influencers }) => {
  return (
    <>
      <div className="Domain">
        <div className="header">Influenced Enterprise Events</div>
        <div className="domainDetails">
          {influencers.map((influencer, index) => {
            return (
              <div key={index} className="domainNames">
                {influencer}
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};

export const DeterminantRules = ({ ruleMapping }) => {
  return (
    <>
      <div className="Domain">
        <div className="header">Determinant Rules</div>
        <div className="domainDetails">
          {ruleMapping.map((val, index) => {
            return (
              <div key={index} style={{ display: 'flex' }}>
                <div className="domainNames" style={{ width: '20%' }}>
                  {val.ruleId}
                </div>
                <div className="domainNames" style={{ width: '80%' }}>
                  {val.ruleConstruct}
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </>
  );
};
